            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>
        </b-row>
      </div>
    </b-card>
    <!-- <modal-nueva-salida />
    <modal-detalle-salida /> -->
  </div>
</template>

<script>
import {
  BCard, BTable, BSpinner, BRow, BCol, BPagination, BFormInput, BDropdown,
  BDropdownItem, BButton, BMedia, BAvatar, BLink, BFormSelect, BBadge,
} from 'bootstrap-vue'
// import vSelect from 'vue-select'
import { onUnmounted } from '@vue/composition-api'
import store from '@/store'
import { avatarText } from '@core/utils/filter'
import ModalUsuarioNuevo from './ModalUsuarioNuevo.vue'
import ModalReestablecerPassword from './ModalReestablecerPassword.vue'
// import ModalNuevaSalida from '@/views/salud/farmacia/salidas-list/ModalNuevaSalida.vue'
// import ModalDetalleSalida from '@/views/salud/farmacia/salidas-list/ModalDetalleSalida.vue'

import usuarioStoreModule from './usuarioStoreModule'

export default {
  components: {
    BBadge,
    BCard,
    BTable,
    BSpinner,
    BFormSelect,
    BRow,
    BCol,
    BFormInput,
    BDropdown,
    BDropdownItem,
    BPagination,
    BButton,
    BMedia,
    BAvatar,
    BLink,

    ModalUsuarioNuevo,
    ModalReestablecerPassword,
    // BBadge,
    // BMedia,
    // BAvatar,
    // BProgress,
    // BCardText,

    // externs
    // ModalDetalleSalida,
    // ModalNuevaSalida,
  },
  data() {
    return {
      isBusy: false,
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 50],
      sortBy: 'id',
      sortDesc: false,
      // sortDirection: 'desc',
      filter: '',
      filterOn: [],

    }
  },
  methods: {
    actualizarTablaUsuario() {
      this.$refs.refTablaUsuariosList.refresh()
    },
    agregarNuevoUsuario() {
      this.$emit('nuevoUsuario')
      // this.$bvModal.show('modalAgregarSalida')
    },
    editarUsuario(item) {
      this.$emit('editarDatosUsuario', item)
      // this.$bvModal.show('modal_detalles_salidas')
    },
    restablecerContrasenia(item) {
      this.$emit('reestablecerPassword', item)
    },
    // eliminarUsuario(item) {
    //   this.$swal({
    //     title: '¿Esta seguro de eliminar al usuario?',
    //     text: 'Esta acción es irreversible',
    //     icon: 'warning',
    //     showCancelButton: true,
    //     confirmButtonText: 'Si, ¡Eliminar!',
    //     customClass: {
    //       confirmButton: 'btn btn-primary',
    //       cancelButton: 'btn btn-outline-danger ml-1',
    //     },
    //     buttonsStyling: false,
    //   }).then(result => {
    //     if (result.value) {
    //       store.dispatch('usuarios-list-store/deleteUsuario', item.id).then(() => {
    //         this.$toast({
    //           component: ToastificationContent,
    //           position: 'top-right',
    //           props: {
    //             title: 'Usuario Eliminado!',
    //             icon: 'CheckCircleIcon',
    //             variant: 'success',
    //           },
    //         })
    //         this.actualizarTablaUsuario()
    //       })
    //         .catch(() => {
    //           this.$toast({
    //             component: ToastificationContent,
    //             position: 'top-right',
    //             props: {
    //               title: '¡Error al eliminar usuario!',
    //               icon: 'CheckCircleIcon',
    //               variant: 'danger',
    //             },
    //           })
    //         })
    //     }
    //   })
    // },
    async myProvider(ctx) {
      console.log('CTX: ', ctx)
      const promise = store.dispatch('usuarios-list-store/getUsuarios', { queryParams: ctx })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        console.log('responseGetIngresosMedicamentos: ', response)
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data
        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0
        this.to = to || 0
        /*
        items.forEach((value, index) => {
          items[index].fecha_venc_dias = this.fecha_menor_en_dias(value.lotes_disponibles)
        }) */

        // Must return an array of items or an empty array if an error occurred
        return items || []
      })
    },
    // getFecha(createdAt) {
    //   const date = new Date(createdAt)
    //   const day = date.getDate()
    //   const month = date.getMonth() + 1
    //   const year = date.getFullYear()

    //   if (month < 10) {
    //     return `${day}-0${month}-${year}`
    //   }
    //   return `${day}-${month}-${year}`
    // },
    // getTime(createdAt) {
    //   const date = new Date(createdAt)
    //   const hours = date.getHours()
    //   const minute = date.getMinutes()
    //   const second = date.getSeconds()
    //   return ` ${hours}:${minute}:${second}`
    // },
    recorteName(name) {
      let recorte = []
      recorte = name.split(' ')
      return `${recorte[0]} ${recorte[1]}`
    },
  },
  setup() {
    const USUARIOS_APP_STORE_MODULE_NAME = 'usuarios-list-store'

    // Register module
    if (!store.hasModule(USUARIOS_APP_STORE_MODULE_NAME)) store.registerModule(USUARIOS_APP_STORE_MODULE_NAME, usuarioStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(USUARIOS_APP_STORE_MODULE_NAME)) store.unregisterModule(USUARIOS_APP_STORE_MODULE_NAME)
    })

    const fields = [
      { key: 'index', label: 'N°', sortable: true },
      { key: 'apellidos_nombres', label: 'APELLIDOS Y NOMBRES', sortable: true },
      { key: 'usuario', label: 'USUARIO', sortable: true },
      { key: 'roles', label: 'ROLES', sortable: true },
      { key: 'estado', label: 'ESTADO', sortable: true },
      { key: 'acciones' },
    ]
    return {
      // Filter
      avatarText,
      fields,
      usuarioStoreModule,
    }
  },
}
</script>
